
document.addEventListener('DOMContentLoaded', () => {
  const storageKey = 'theme';
  const firstVisitKey = 'first-visit';
  const isFirstVisit = !localStorage.getItem(firstVisitKey);

  if (isFirstVisit) {
    localStorage.clear();
    localStorage.setItem(firstVisitKey, 'true');
  }

  const getColorPreference = () => {
    const savedTheme = localStorage.getItem(storageKey);
    return savedTheme || 'dark';
  };

  const theme = {
    value: getColorPreference(),
  };

  const setTheme = (themeValue, setClassName) => {
    document.documentElement.setAttribute('data-theme', themeValue);

    if (setClassName) {
      const icons = document.querySelectorAll('.theme-switch__icon');
      icons.forEach(icon => icon.classList.toggle("theme-switch__icon--active", icon.classList.contains(themeValue)));
    }

    const logo = document.querySelector('.logo__img');
    if (logo) {
      logo.src = themeValue === "dark" ? "images/logo.svg" : "images/logo-white.svg";
    }

    localStorage.setItem(storageKey, themeValue);
  };

  const setPreference = () => {
    setTheme(theme.value, true);
  };

  const keepTheme = (setClassName) => {
    const savedTheme = localStorage.getItem(storageKey);

    if (savedTheme) {
      setTheme(savedTheme, setClassName);
    } else {
      const defaultTheme = 'dark';
      setTheme(defaultTheme, setClassName);
      localStorage.setItem(storageKey, defaultTheme);
    }
  };

  const onClick = () => {
    theme.value = theme.value === 'light' ? 'dark' : 'light';
    setPreference();
  };

  keepTheme(true);

  const themeSwitch = document.querySelector('.theme-switch');
  if (themeSwitch) {
    themeSwitch.addEventListener('click', onClick);
  }

  window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', ({
    matches: isDark
  }) => {
    if (theme.value === 'dark') {
      theme.value = isDark ? 'dark' : 'light';
      setPreference();
    }
  });
});
